 .nav {
    position: fixed;
    left: 0;
    top: 0;
    transform: translateX(0);
    height: 100vh;
    width: 300px;
    background: #191d2b;
    border-right: 1px solid #2e344e;
    z-index: 10;
    transition: all .4s ease-out;
}

.rounded-circle{
    border: 7px solid #2e344e;
}

.image-container {
    padding: 40px 0;
    display: block;
    width: 100%;
    text-align: center;
    border-bottom: 1px solid #2e344e;
}

.inner {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    overflow: hidden;
    overflow-y: auto;
}

.header-menu {
    width: 100%;
    padding: 15px 5px;
    margin-bottom: 18em;
}

.header-menu li {
    list-style: none;
    text-align: center;
    display: block;
    margin-bottom: 0.8em;
}


.header-menu li a {
    text-transform: uppercase;
    font-size: 1.3rem;
    letter-spacing: 1px;
    display: block;
    padding: 5px 0;
    color: #a4acc4;
    transition: all .4s ease-out;
    font-weight: 600;
    position: relative;
    overflow: hidden;
    z-index: 1;
}

.header-menu li a:hover {
    text-decoration: none;
    background-color: #153055;
    color: #0B7FFF;
}